import React, { useState } from 'react';
import './Project.css';



function PasswordStrength() {
    const isNumRegex = /\d/;
    const isSpecialCharRegex = /[ !@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/;

    const [minChar, setMinChar] = useState(false);
    const [num, setNum] = useState(false);
    const [specialChar, setSpecialChar] = useState(false);
    const [createAcount, setCreateAcount] = useState(false);


    const [password, setPassword] = useState('');



    const handleSubmit = (e) => {
        e.preventDefault();
        setCreateAcount(!createAcount);
    }

    const onChangePassword = password => {
        setPassword(password);
        { password.length >= 8 ? setMinChar(true) : setMinChar(false) }
        { isNumRegex.test(password) ? setNum(true) : setNum(false) }
        { isSpecialCharRegex.test(password) ? setSpecialChar(true) : setSpecialChar(false) }

    }





    return (
        <div>
            <article className="article">
                {!createAcount &&
                    <form className="form" onSubmit={handleSubmit}>
                        <h2>Password Validation</h2>
                        <label for="email">Enter your email address</label>
                        <input
                            type="text"
                            id="email"
                            name="email"



                            required
                        />

                        <label for="password">Enter your password</label>
                        <input
                            type="password"
                            name="password"
                            id="password"
                            value={password}
                            onChange={(e) => onChangePassword(e.target.value)}
                            required />

                        {!minChar && <p> Must be at least 8 characters</p>}
                        {!num && <p> Must contain at least 1 number</p>}
                        {!specialChar && <p> Must contain at least 1 special character</p>}

                        {minChar && num && specialChar && <button className="btn" type="submit">Submit</button>}
                    </form>}
                {createAcount && <h3>Thank you for joining us</h3>}

            </article>
        </div>
    )
}

export default PasswordStrength
