import React, { useState } from 'react';
import './Project.css';

function PaintCalc() {
const [option, setOption] = useState('singleWall');
const [length, setLength] = useState(0);
const [width, setWidth] = useState(0);
const [coverage, setCoverage] = useState(0);
const [area, setArea] = useState(0);
const [calc, setCalc] = useState(false);
const [reqPaint, setReqPaint] = useState(0);


const onChangeSelect = option => {
    setCalc(false);
    setCoverage(0);
    setOption(option);
    console.log(option);
}

const handleSubmit = (e) => {
    e.preventDefault();
   {option === 'singleWall' ?  calculateSingle() : calculateWall()}
    setCalc(true)
}

function calculateSingle (){
    const reqArea = (length * width);
    const reqPaint = (reqArea/coverage);
    setReqPaint(reqPaint);
    console.log(reqPaint);
    return
}

function calculateWall (){
    const reqPaint = (area/coverage);
    setReqPaint(reqPaint);
    return
}

    return (
        <div>
            <article className="article">
                <div>
                <h2>Paint Quantity Calculator</h2>
                <label for="option">Select your function</label>
<select 
value={option}
onChange={(e) => onChangeSelect(e.target.value)}
id="option" 
name="function">
<option value="singleWall">Single Wall</option>
<option value="totalArea">Total Area</option>
</select>

{option === 'singleWall' && <><h3>Calculator</h3>
            <form onSubmit={handleSubmit}>
            <label for="length">Length m </label>
  <input 
  type="number" 
  name="length" 
  id="length" 
  value={length}
  onChange={(e) => setLength(e.target.value)}
  required/>
  <label for="width"> Width m </label>
  <input 
  type="number" 
  name="width" 
  id="width" 
  value={width}
  onChange={(e) => setWidth(e.target.value)}
  required/>
  <label for="coverage"> Coverage m^2/L </label>
    <input 
  type="number" 
  name="coverage" 
  id="coverage" 
  value={coverage}
  onChange={(e) => setCoverage(e.target.value)}
  required/>
  <button className="btn" type="submit">Submit</button>
  {calc && <p> You require {Math.ceil(reqPaint)} Litres of paint</p>}
            </form></>}

            {option === 'totalArea' && <><h3>Calculator</h3>
            <form onSubmit={handleSubmit}>
            
  <label for="area"> Area m^2 </label>
  <input 
  type="number" 
  name="area" 
  id="area" 
  value={area}
  onChange={(e) => setArea(e.target.value)}
  required/>
  <label for="coverage"> Coverage m^2/L </label>
    <input 
  type="number" 
  name="coverage" 
  id="coverage" 
  value={coverage}
  onChange={(e) => setCoverage(e.target.value)}
  required/>
  <button className="btn" type="submit">Submit</button>
  {calc && <p> You require {Math.ceil(reqPaint)} Litres of paint</p>}
            </form></>}       



            </div>
            </article>
        </div>
    )
}

export default PaintCalc
