import React, { useState } from 'react';
import './Project.css';




function PalindromeCheck() {

    const [result, setResult] = useState();
    const [checked, setChecked] = useState(false);
    const [str, setStr] = useState('');


    const checkPalindrome = (str) =>{
        str = str.replace(/\W/g, '');
        str = str.toLowerCase();
        for (let i=0; i<str.length; i+=1){
            if (str[i] !== str[str.length - 1 - i]){
                setResult(false);
                return;
            }
        }
        setResult(true);
        return;
    }
    
const onChangeSet = (e) =>{
    setChecked(false);
    setStr(e);
}


    const handleSubmit = (e) => {
        e.preventDefault();
      checkPalindrome(str);
     setChecked(true);
    }
    return (
        <div>
            <article className="article">
            
            <form className="article-form" onSubmit={handleSubmit}>
            <h2 className="article-title">Palindrome Checker</h2>
            <p>Input your string</p>
            <label for="string">Your String</label>
            <input
            type="text"
            name="string"
            id="string"
            value={str}
            onChange={(e) => onChangeSet(e.target.value)}
            required />
            <button className="btn" type="submit">Check</button>
            {checked && result && <p> {str} is a palindrome</p> }
             {checked && !result && <p>{str} is not a palindrome</p>}
             </form>

             </article>
        </div>
    )
}

export default PalindromeCheck
